.about-contacts__h2 {
	font-family: 'Cormorant Unicase';
	font-weight: 400;
	.fSize(36vw, 24px, 36px, 640px, 1280px);
	text-align: center;
	position: relative;
	&:before {

		.ba();
		height: auto;
		content:"Контакти";
		color: #ececec;
		font-family: 'Cormorant Infant',serif;
    font-weight: 400;
    .fSize(140vw, 110px, 140px, 640px, 1280px);
    top:-100%;
    z-index: -1;
    display: block;
    top: -190%;
	 }
}

.about-contacts__top {
	margin-right: -30px;
	margin-left: -30px;
	width: 100%;
	.flex();
	align-items: stretch;
	.fM(@mt:0, @mb: 87%, 0, 40px, 480px, 1280px);
}

.about-contacts__object {
	text-align: center;
	margin: 0 15px;
	flex: 0 1 30%;
	height: auto;
	.flex(@flex-direction: column, @justify-content:space-between);
}

.about-contacts__name {
	font-family: 'MuseoSansRegular', sans-serif;
	margin-bottom: 25px;
}

.about-contacts__phone {
	.fSize(30vw, 21px, 30px, 640px, 1280px);
	font-family: 'MuseoSansMedium', sans-serif;
	// margin-top: auto;
}

.about-contacts__bottom {
	width: 100%;
	text-align: center;
}

.about-contacts__email {
	color: #e81d6f;
	.fSize(30vw, 21px, 30px, 640px, 1280px);
	font-family: 'MuseoSansRegular', sans-serif;
}

.about-contacts__desc {
	font-family: Cormorant,serif;
	font-weight: 500;
	margin-bottom: 45px;
	font-style: italic;
	.fSize(24vw, 18px, 24px, 900px, 1280px);

}