.knowledge {
	padding-bottom: (156%/1920*50);
	padding-top: (139.5%/1920*100);
	.f-container {
		margin: 0 -38.5px;
		justify-content: center;
	}
}

.knowledge__card {
	box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
	margin: 0;
	padding: 12px 12px 0px 12px;
	background: #fff;
	max-width: 387px;
	margin-right: 32.5px;
	margin-left: 32.5px;
	margin-top: 28px;
	// &:not(:nth-child(3)){
	// 	margin-right: 20px;
	// }

}

.knowlege__title, .knowledge__card-description {
	span {
		color: #e00058;
	}
}

.knowledge__card-description {
	font-family: MuseoSansMedium,sans-serif;
	font-weight: 500;
	font-size: 16px;
	padding: 60px 35px;
	text-align: center;
}

.knowlege__title {
	// margin-bottom: 	28px;
	position: relative;	
	z-index: 2;
	&:before {
		content: 'Pro весілля';
		position: absolute;	
		top: 0;
		left: -10px;
		color: #ececec;
		font-size: (140vw/1280*100);
		z-index: -1;
		font-family: 'Cormorant', serif;
		font-weight: 300;
		top: -74%;
		display: inline-block;
		letter-spacing: 11.2px;

	}
}

.knowledge__btn{
	    max-width: 338px;
    padding-top: 11px;
    padding-bottom: 11px;
    margin: 0 auto;
    margin-top: 52px;
}