@font-face {
	font-family: 'MuseoSansThin';
	src:url(../fonts/MuseoSansCyrl.ttf) format("truetype");
	font-weight: 100;
	font-style: normal;
}


@font-face {
	font-family: 'MuseoSansRegular';
	src:url(../fonts/MuseoSansCyrl_0.ttf) format("truetype");
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'MuseoSansMedium';
	src:url(../fonts/MuseoSansCyrl_1.ttf) format("truetype");
	font-weight: 500;
	font-style: normal;
}


@font-face {
	font-family: 'Myriad';
	src:url(../fonts/MyriadPro-Regular.ttf) format("truetype");
	src:url(../fonts/MyriadPro-Regular.eot) format("embed-truetype");
	src:url(../fonts/MyriadPro-Regular.woff) format("woff");
	font-weight: 400;
	font-style: normal;
}


