.about-offer__inner {

	padding-top: (164%/1920*100);
	padding-bottom: (68%/1920*100);
	// background: url(../img/about/about_bg.png) no-repeat 55% 0;
	background-size: contain;

	.f-container {
		flex-direction: column;
		align-items: flex-start;
	}
}

.about-h2 {
	margin-bottom: 	50px;
}


.about-offer__title {
	.fSize(72vw, 40px, 72px, 640px, 1280px);
	font-family: 'Cormorant', sans-serif;
	font-weight: 400;
	margin-bottom: 8px;
}

.about-offer__description {
	.fSize(24vw, 18px, 24px, 940px, 1280px);
	font-family: 'Cormorant', serif;
	font-style: italic;
	&:nth-child(2) {
		padding-left: (66vw/1920*100);
	}

	&:nth-child(3) {
		padding-left: (107vw/1920*100);
	}

	&:nth-child(4) {
		padding-left: (150vw/1920*100);
	}
}
.about-offer__top {
	padding-bottom: (165%/1280*100);
	padding-top: ((320%-164%)/1280*100);
	background: url(../img/about/about_bg.png) 63% 25% no-repeat;
	background-size: 72%;
	width: 100%;
}
.about-offer__middle {
	width: 100%;
	.fM(@mt:0, @mb: 80%, 0, 40px, 480px, 1280px);
}
.searching-form__title {
	text-align: center;
	.fSize(46vw, 28px, 46px, 640px, 1280px);
	font-family: 'Cormorant', sans-serif;
	font-weight: 400;
	margin-bottom: 40px;
}

.searching-form-wrap {
	padding: 63px 74px 70px;
	background: rgba(255,255,255,.75);
  margin: 0 auto;
  box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
  width: 100%;
  position: relative;
  z-index: 3;
}

.searching-form__layer {
	width: 100%;
	.flex();
}

.searching-form {
	.flex();
}

.searching-form__btn {
	margin: 0;
	max-width: 290px;
	padding: 28.5px 25px;
}

.searching-form__select-wrap {
	position: relative;
	margin-right: 25px;
  display: inline-block;
  width: 100%;
  max-width: 364px;
  &:after {
    content: "▼";
    padding: 0 8px;
    font-size: 12px;
    position: absolute;
    right: 8px;
    top: 42%;
    z-index: 1;
    text-align: center;
    width: 10%;
    height: 100%;
    pointer-events: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
}

.searching-form__select {
	outline: none;

	padding-right: 25px;
  appearance: none;
  font-family: MuseoSansMedium,sans-serif;
    font-weight: 500;
    background: #fff;

    box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    border: none;
   
    transition: .2s linear;
    width: 100%;
    border-radius: 6px;
    font-size: 16px;
    padding: 28.5px 25px 28.5px 36px;
    max-width: 364px;
}

.about-text {
	font-family: Myriad,sans-serif;
	font-style: normal;
	font-size: 16px;
	text-align: center;
	width: 	100%;
	line-height: 2;
	margin-bottom: 	63px - (32/4);
}

.about-offer__text {
	margin-bottom: 	0;
}

.about-offer__bottom {
	width: 	100%;
}