


.offer {

	padding-top: 324px;
	padding-bottom: (139.5%/1920*100);
	background: url(../img/header_bg.png) no-repeat center 0%;
	background-size: cover;
}





.offer__h1 {
	font-family: 'Cormorant', serif;
	font-weight: 400;
	font-size: calc( (100vw - 480px)/(1280 - 480) * (75 - 32) + 32px);
	// font-size: pi() * 2;

	// font-size: (68vw/1280*100);
	max-width: (820%/ 1280 * 100);
	min-width: 363px;
	line-height: 0.85;
	margin-bottom: 25px;
	letter-spacing: 4px;
	// .text3d(#242424);

}

.offer__description {
	font-family: 'MuseoSansMedium', sans-serif;
	line-height: 1.65;
	max-width: (630%/ 1280 * 100);
	min-width: 300px;
}

.offer-form__title {
	font-family: 'Cormorant', serif;
	font-size: (46vw/1280*100);
	font-weight: 400;
	text-align: center;
	margin-bottom: 26px;
}

.offer-form__select-layer {
	width: 100%;
	max-width: 364px;
	&:first-child{
		margin-right: 20px;
	}
}

 .offer-form__select {
	font-family: 'MuseoSansMedium', sans-serif;
	font-weight: 500;
	background: rgba(255,255,255,1);
	box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
	border: none;
	transition: all 0.2s linear; 
	// border-bottom: 1px solid #b03466;
	width: 100%;
	padding-bottom: 24.5px;
	padding-left: 36px;
	padding-right: 25px;
	padding-top: 24.5px;
	border-radius: 6px;
	font-size: 16px;


	&:focus {

		outline: none;

	}
	.offer-form__option {
		font-family: 'MuseoSansMedium', sans-serif;
		background: transparent;
	}
}

.offer-form__btn {
	max-width: 338px;
	padding-top: 11px;
	padding-bottom: 11px;
	margin: 0 auto;
}

.offer-form {
background: rgba(#fff, 0.75);
margin: 0 auto;
padding: 40px 58px 52px;
box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
width: 100%;
max-width: 900px;
position: relative;
z-index: 3;
}

.offer-form__select-wrapper {
	margin-bottom: 40px;
	width: 	100%;
	.flex(); 
}

.offer__title-block {
	margin-bottom: 11%;
}


.offer-form__select-layer {
  position: relative;
  display: inline-block;
}
.offer-form__select-layer:after {
  content: "▼";
  padding: 0 8px;
  font-size: 12px;
  position: absolute;
  right: 8px;
  top: 42%;
  z-index: 1;
  text-align: center;
  width: 10%;
  height: 100%;
  pointer-events: none;
  box-sizing: border-box;
}
.offer-form__select {
  padding-right: 25px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}