@media screen and (max-width: 1920px) {
.about-offer__top {
	background-position-y: 12%; 
}
}

@media screen and (max-width: 1110px) {
	.searching-form {
		flex-wrap: wrap; 
		justify-content: space-between;
	}

	.about-text {
		br{
			display: none;
		}
	}

	.searching-form-wrap {
		padding-left: 40px;
		padding-right: 40px;
		padding-top: 40px;
		padding-bottom: 40px;
	}

	.searching-form__select {
		padding-left: 15px;
		font-size: 14px;
	}


	.searching-form__btn {
		margin: 0 auto;
		margin-top: 25px;
	}
}

@media screen and (max-width: 860px) {
	.about-more__card {
		flex-direction: column;
	}

	.about-more__bottom {
		.flex();
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.about-more__card {
			width: 100%;
			&:not(:last-child) {
					margin-bottom: 40px
			}

			&:last-child {
				margin-bottom: 0;
			}
			.flex();
			flex-direction: column;
			align-items: center;
			&:nth-child(4) {
				flex-direction: column-reverse;
			}
			.about-more__descr {
				margin-right: 0;
				margin-left: 0;
				text-align: center;
				padding-top: 0;
			}
			.about-more__img {
				margin-left: 0;
				margin-right: 0;
				margin-bottom: 25px;
			}
		}
	}
	.about-text {
		margin-bottom: 40px;
	}
}


@media screen and (max-width: 640px) {
	.about-offer__top {
		max-width: 420px;
		margin: 0 auto;
		text-align: center;
		background: url(../img/about/about_bg.png) no-repeat right top/cover; 
		position: relative;
		padding-top: 72px;
		& > * {
			position: relative;
			z-index: 2;
		}
		&:before {
			.ba();
			background: rgba(255,255,255,0.6);
		}
		.about-offer__description {
			padding-left: 0;
		}
	}
	.about-offer__title {
		margin-bottom: 15px;
	}
	.about-offer__description {
		display: inline;
	}

	.searching-form__layer {
		flex-direction: column;
	}

	.searching-form__select-wrap {
		&:first-child {
			margin-bottom: 25px;
		}
		margin-right: 0;
		max-width: 290px;
	}

	.searching-form__select {
		font-size: 12px;
	}

	

	.about-h2 {
		&:before {
			display: none;
		}
	}

	.about-contacts__top {
		flex-direction: column;
		margin-left: 0;
		margin-right: 0;
	}

	.about-contacts__object {
		margin-right: 0;
	}

	.about-more__inner {
		padding-bottom: 35px;
		padding-top: 20px;
	}

	.about-text {
		margin-bottom: 30px;
		line-height: 1.75;
	}
.about-more__card {
	&:first-child {
		.about-more__img {
			width: 18vw;
		}
	}

	&:nth-child(2) {
		.about-more__img {
			width: 30vw;
		}
	}
	.about-more__img {
		width: 100%;
		img {
			margin: 0 auto;
			// width: 25vw;
			}
		}
	}

	.about-more__descr {
		min-width: 280px;

	}
}




