.header {
	padding-top: (40%/1920*100);

	padding-bottom: (40%/1920*100);
	background: #fff;
	transition: all 0.25s ease;
	position: relative;	
	z-index: 5;
	position: fixed;
	width: 100%;
	// position: relative;
	box-shadow: 0 3px 5px -5px rgba(0,0,0,0.45);
	.center {
		transition: all 0.25s linear;
	}
	.f-container{

	}
}
// .header-nav {
// 	margin-right: 30px;
// }

.header-nav__link {
	font-family: 'MuseoSansMedium', sans-serif;
	text-transform: uppercase;
	transition: all 0.2s cubic-bezier(0.1, 0.7, 1.0, 0.1);
	&:active {
		color: #e00058;
		text-shadow: 2px 1px 3px rgba(#e00058 ,0.75);
	}

	&:hover {
		color: #e00058;

	}

}

.header-nav__list {
	.flex(@justify-content: center);
}

.header-controls__social {
	.flex();
}

.header-nav__link{
	position: relative;
	font-size: (14vw/1280*100);

	
}


.header-nav__list-item {
	margin-left: 28px;
	letter-spacing: 0.2px;
	cursor: pointer;
	.flex();
	&:first-child {
		margin-left: 0;
	}

	&:last-child {
		margin-right: 28px;
	}

		&:hover {
		.header-nav_heart{
			animation: heart_rot 0.2s linear;
		}
	}

	.header-nav_heart {
		background: url(../img/iconfinder_heart_299063.svg) no-repeat center;
		background-size: contain;
		width: 13px;
		height: 12px;
		margin-right: 12px;
		top: 50%;
		// transform: translateY(-50%) rotate(0);
		left: -18px;
		transition: 0.4s linear all;
		
		
	}
}

.header-controls__control, .header-controls {
	.flex();
	font-family: MuseoSansMedium,sans-serif;
	font-weight: 500;
	font-size: 14px;
}

.header-controls, .header-nav {
	flex: 0 1 41%;
}

.header-nav {
	margin-left: auto;
}

.header-controls {
	flex: 0 1 27%;
}

.header-logo {
	flex: 0 1 19%;
	min-width: 95px;
	margin-right: 20px;
}

.header-controls__control {
	position: relative;
	flex: 0 1 65%;
	margin-right: 20px;
}

.header-controls__link-wrapper_usable {
	.flex();
	position: relative;
	&:before {
		position: absolute;
		content: "";
		width: 1px;
		height: 15px;
		background: #e00058;
		top: 0;
		left: 32%;
	}
}


.header-controls__link {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 5px;
	transition: all 0.2s linear;
	width: 26px;
	height: 25px;
	position: relative;
	margin-right: 3.5px;
	border-radius: 7px;
	width: 100%;
	// min-width: 200px;
	max-width: 40px;
	min-width: 40px;
	height: 40px;
	max-height: 40px;
	box-shadow: rgba(0, 0, 0, 0.45) 0px 2px 15px -6px;
	border: none;
	&:hover {
		transform: scale(1.02) translateY(-6px);
		// box-shadow: 2px 2px 2px rgba(0,0,0,0.5);

	}
}


@keyframes heart_rot {
	to {
		transform: rotate(360deg);
	}
}


.header-controls__search-wrapper {
	position: relative;
	transition: all 0.4s linear;
	margin-right: 30.5px;
	border-radius: 7px;
	width: 100%;
	// min-width: 200px;
	max-width: 40px;
	min-width: 40px;
	height: 40px;
	max-height: 40px;
	box-shadow: rgba(0, 0, 0, 0.45) 0px 2px 15px -6px;
	border: none;
	.header-controls__search-wrapper-before{
		cursor: pointer;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 10px;
		width: 20px;
		height: 20px;
		z-index: 6;
		pointer-events: none;
		background: url(../img/look.png) no-repeat center;
		background-size: contain;
	}
}

.header-controls__search {
	width: 100%;
	position: relative;
	z-index: 5;
	height: 100%;
	border-radius: 5px;
	font-size: 14px;
	padding: 10px 5px;
	text-align: left;
	padding-left: 15px;
	padding-right: 15px; 
	&::-webkit-input-placeholder {
		color: rgb(41, 40, 40);
	}

	&::-moz-placeholder {
		color: rgb(41, 40, 40);
	}

	// &:focus {
	// 		animation: bord 0.5s linear forwards;
	// 	}
	}


.header-controls__link_usable {
	letter-spacing:0.3px;
	&:first-child{
		margin-right: 26.5px;
	}
}


@keyframes bord {
	0% {
		border: 1px;
	}

	25% {
		border-left: 2px solid #e00058;
		
	}

	50% {
		border-top: 2px solid #e00058;
		
	}

	75% {
		border-right: 2px solid #e00058;
		
	}

	100% {
		border: 2px solid #e00058;
	}
}


@keyframes bord2 {
	0% {
		border: 1px;
	}

	25% {
		border-right: 2px solid #e00058;
		
	}

	50% {
		border-top: 2px solid #e00058;
		
	}

	75% {
		border-left: 2px solid #e00058;
		
	}

	100% {
		border: 2px solid #e00058;
	}
}

//|||||||||||||||||||||||||||||||||| header mobile||||||||||||||||||||||||||||||||||||||||||//


.header.fixed {
	position: fixed;
	width: 	100%;
	z-index: 999;
	padding: (15%/1920*100) 0;
}

.hamburger__menu-wrap {
	width: 45px;
	height: 45px;
	display: none;
	cursor: pointer;
	&:hover {
		.hamburger__line {
			background: #e00058;
			animation: stretch 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
		}
	}
}


@keyframes stretch {
	0% {
		transform: scaleX(1);
	}

	50% {
		transform: scaleX(1.28);
	}

	100% {
		transform: scaleX(1);
	}
}
.hamburger__menu {
	width: 100%;
	height: 100%;
	.flex(@flex-direction:column, @justify-content:center, @align-items:flex-end);

}
.hamburger__line {
	width: 30px;
	height: 3px;
	display: block;
	border-radius: 15px;
	transition: all 0.3s linear;
	background: #292828;
	&:nth-child(2) + &, &:nth-child(2) {
		margin-top: 5px;
	}

	&:nth-child(2) {
		width: 20px;
		
	}

	&:nth-child(3) {
		width: 25px;
		align-self: flex-end;
		
	}
}

.menu__layer {
	position: absolute;
	width: 200%;
	height: 100vh;
	top: 0;
	left: -200%;
	background: #e00058;
	z-index: 1002;

}

.menu__table{
	padding: 0 25px;
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	z-index: 1001;
	background: #fff;
	.flex(@justify-content:center, @flex-direction:column);
	display: none;
}

.menu__table-nav {
	width: 100%;
}

.menu__table-ul {
	.flex(@flex-direction: column);
	width: 100%;
	margin-bottom: 25px;
}

.menu__table-item {
	&:not(:last-child) {
		margin-bottom: 15px;
	} 
	position: relative;
	width: auto;
	opacity: 0;
	left: -80px;
	min-width: 280px;
	width: 	100%;
	display: flex;
	margin-left: 0;
	justify-content: center;
	transition: left .4s ease-out .3s,opacity .4s ease-out;
	&:first-child{
		transition-delay: 0.15s;
	}

	&:nth-child(2) {
		transition-delay: 0.2s;
	}

	&:nth-child(3) {
		transition-delay: 0.25s;
	}

	&:nth-child(4) {
		transition-delay: 0.3s;
	}
}

.menu__table.active-table {
	.menu__table-item {
	opacity: 1;
	left: 0;
	transition: left .4s ease-out ,opacity .4s ease-out;
	&:first-child{
		transition-delay: 0.15s;
	}

	&:nth-child(2) {
		transition-delay: 0.2s;
	}

	&:nth-child(3) {
		transition-delay: 0.25s;
	}

	&:nth-child(4) {
		transition-delay: 0.3s;
	}
	}
}

.menu-nav_heart {
	.header-nav__list-item.header-nav_heart();
}

.menu__table-link {
	// .header-nav__link();
	font-size: 20px;
}

.menu__table_social {
	margin-bottom: 	25px;
	.flex();
	min-width: 135px;
	display: none;
}

.menu__table-close-wrap {
	width: 45px;
	cursor: pointer;
	height: 45px;
	position: absolute;
	top: 2%;
	right: 10px;
	&:hover {
		.menu__table-close {
			transform: rotate(45deg);
			
		}

		.menu__tavble-close-line:nth-child(1) {
			width: 0;

		}

		.menu__tavble-close-line:nth-child(2) {
			// width: 0;
			background: #e00058;
		}
	}
}

.menu__table-close {
	width: 100%;
	height: 100%;
	position: relative;
	transition: all 0.2s linear;
}

.menu__tavble-close-line {
	display: block;
	
	width: 30px;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border-radius: 10px;
	margin: auto;
	height: 3px;
	background: #292828;
	&:first-child{
		transform:  rotate(45deg);
		
	}

	&:last-child{
		transform: rotate(135deg);
		transition: 0.2s linear all;
	}
}

.menu__table-link_social {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 5px;
	transition: all 0.2s linear;
	width: 26px;
	height: 25px;
	position: relative;
	margin-right: 3.5px;
	border-radius: 7px;
	width: 100%;
	// min-width: 200px;
	max-width: 40px;
	min-width: 40px;
	height: 40px;
	max-height: 40px;
	box-shadow: rgba(0, 0, 0, 0.45) 0px 2px 15px -6px;
	border: none;
	&:hover {
		transform: scale(1.02) translateY(-6px);
		// box-shadow: 2px 2px 2px rgba(0,0,0,0.5);

	}
}

.menu__table_search-wrapper {
	display: none;	
	// visibility: hidden;	
	position: relative;
    -webkit-transition: .4s linear;
    -o-transition: .4s linear;
    -moz-transition: .4s linear;
    transition: .4s linear;
    // margin-right: 30.5px;
    -moz-border-radius: 7px;
    border-radius: 7px;
    width: 100%;
    max-width: 214px;
    // min-width: 40px;
    height: 50px;
    max-height: 50px;
    -webkit-box-shadow: rgba(0,0,0,.45) 0 2px 15px -6px;
    -moz-box-shadow: rgba(0,0,0,.45) 0 2px 15px -6px;
    box-shadow: rgba(0,0,0,.45) 0 2px 15px -6px;
    border: none;
}

.menu__table__search-wrapper-before {
	cursor: pointer;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 10px;
    width: 20px;
    height: 20px;
    z-index: 6;
    // pointer-events: none;
    background: url(../img/look.png) center/contain no-repeat;
}

.menu__table__search {
	width: 85%;
    position: relative;
    z-index: 5;
    height: 100%;
    -moz-border-radius: 5px;
    border-radius: 5px;
    font-size: 18px;
    padding: 10px;
    text-align: left;
}

.active-input {
	width: 100%; 
    max-width: 100%;
    position : absolute;
    background-color : #fff;
    z-index : 3;
    // min-width: 240px;
    transition: all 0.4s linear;
    .header-controls__search-wrapper-before {
    	pointer-events: all;
    }

    .header-controls__search {
    	width: 	85%;
    }
}

.menu__table.active-table {
	.menu__table_search-wrapper {

		animation: fadeIn 1.35s linear forwards;
	}
}

.menu__table {
	.menu__table_search-wrapper {

		animation: fadeOut .35s linear;
	}
}

.header-controls__link_usable_mobile {
    display: flex;
    display: none;
    justify-content: center;
    align-items: center;
    transition: .2s linear;
    position: relative;
    margin-right: 3.5px;
    border-radius: 7px;
    width: 100%;
    max-width: 40px;
    min-width: 40px;
    height: 40px;
    max-height: 40px;
    box-shadow: rgba(0,0,0,.45) 0 2px 15px -6px;
    border: none;


    svg {
    	width: 20px;
    	height: 20px;  
    	fill: #e00058; 
    	 }
    &:hover {
    	transform: scale(1.08);
    }
}