html, body {
	overflow-x: hidden; 

	

}

::selection {
		background: #e81d6f;
		color: #fff;
	}
body 
{
	margin: 0;
	padding: 0;	
		
	background-attachment: fixed;
	
	font-family: 'Cormorant Infant', serif;	
	font-size: 16px;
	font-weight: normal;
	color: #292828;
	letter-spacing: 1.15px;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
	color: #292828;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: #292828;
	cursor: pointer;

}


input {
	width: 100%;
	outline: none;
	border: none;
	text-align: center;
}


main {
	color: #000;
}

.center {
	max-width: 1330px;
	width: 100%;
	margin: 0 auto;
	padding: 0 25px;

}



.f-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}












//POPUP


/* Styles for dialog window */
#small-dialog {
	background: white;
	padding: 20px 30px;
	text-align: left;
	max-width: 400px;
	margin: 40px auto;
	position: relative;
}


/**
 * Fade-zoom animation for first dialog
 */

/* start state */
.my-mfp-zoom-in .zoom-anim-dialog {
	opacity: 0;

	-webkit-transition: all 0.2s ease-in-out; 
	-moz-transition: all 0.2s ease-in-out; 
	-o-transition: all 0.2s ease-in-out; 
	transition: all 0.2s ease-in-out; 



	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 
}

/* animate in */
.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
	opacity: 1;

	-webkit-transform: scale(1); 
	-moz-transform: scale(1); 
	-ms-transform: scale(1); 
	-o-transform: scale(1); 
	transform: scale(1); 
}

/* animate out */
.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 

	opacity: 0;
}

/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
	opacity: 0;
	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
	opacity: 0.75;
}
/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0;
}

.mfp-close-btn-in .mfp-close {
	top: 7px;
	right: 15px;
	opacity: 1;
	color: transparent;
	&:hover:before {
		transform: rotate(180deg);
	}
	&:hover:after {
		transform: rotate(0deg);
	}

	&:before, &:after {
		content: "";
		position: absolute;
		width: 35px;
		height: 4px;
		background: #000;
		top: 50%;
		left:5px;
	}

	&:before {
		transform: rotate(135deg);
		transition: all 0.2s linear;
	}

	&:after {
		transform: rotate(45deg);
		transition: all 0.2s linear;
	}
}




/**
 * Fade-move animation for second dialog
 */

/* at start */
.my-mfp-slide-bottom .zoom-anim-dialog {
	opacity: 0;
	-webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;

	-webkit-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-moz-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-ms-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-o-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );

}

/* animate in */
.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
	opacity: 1;
	-webkit-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-moz-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-ms-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-o-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
}

/* animate out */
.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
	opacity: 0;

	-webkit-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-moz-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-ms-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-o-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
}

/* Dark overlay, start state */
.my-mfp-slide-bottom.mfp-bg {
	opacity: 0;

	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing.mfp-bg {
	opacity: 0;
}




// POPUP


//|||||||||||||||||||COMMON ELEMENTS|||||||||||||||||||//


.main-btn {
	
	    max-width: 338px;
    padding-top: 11px;
    padding-bottom: 11px;
    margin: 0 auto;
    text-align: center;
	outline: none;
	border-radius: 6px;
	border: none;
	background-image: linear-gradient(to right, #e81d6f, #ea296e, #eb336d, #ed3c6c, #ee446b);
	font-family: 'MuseoSansMedium', sans-serif;
	color: #fff;
	width: 100%;
	letter-spacing: 0.75px;
	transition: 0.35s all linear;
	cursor: pointer;
	display: block;	
	&:focus {
		background: #292828;
		border: 1px solid #b03466;
		box-shadow: 3px 5px 10px rgba(#b03466, 0.75)
	}

	&:hover {
		background: #292828;
	}
}

.fixed{
	
}



.h2-title {
	font-size: (72vw/1280*100);
	font-weight: 400;
	letter-spacing: 5.5px;
}




.slick-filter-add {
	opacity: 0;
	animation: fadeIn 1.15s linear forwards;
}


@keyframes fadeIn{
	to {
		opacity: 1;
	}	
}

@keyframes fadeOut{
	to {
		opacity: 0;
	}	
}



