.foo__form-wrapper {
	width: 100%;
}
.foo__inner {
	.fP(@pt:70%, @pb: 154%, 25px, 55px, 640px, 1920px);
}
.foo__h2, .foo__description {
	text-align: center;
}

.foo__description {
	.fM(@mt:0, @mb: 47%, 0, 35px, 840px, 1280px);
	font-family: "MuseoSansRegular", sans-serif;
	.fSize(24vw, 18px, 24px, 900px, 1280px);
}

.about-h2.foo__h2 {
	margin-bottom: 0;
	font-family: 'Cormorant';
	letter-spacing: 0.35px;
	margin-bottom: 20px;

}

.foo__input {
	&::-webkit-input-placeholder {
		font-family: 'MuseoSansMedium', sans-serif;
		color:#292828;
	}

	&::-moz-placeholder {
		font-family: 'MuseoSansMedium', sans-serif;
		color: #292828;
	}
	&:not(:last-child) {
		outline: 0;
    appearance: none;
		display: block;
    font-family: 'MuseoSansMedium',sans-serif;
    font-weight: 500;
    background: #fff;
    box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    border: none;
    transition: .2s linear;
    width: 100%;
    border-radius: 6px;
    font-size: 16px;
    padding: 26px 25px 26px 54px;
    text-align: left;
    max-width: 1015px;
    margin: 0 auto;
    color: #292828;
    	&:first-child {
		margin-bottom: 30px;
	}

	&:nth-child(2) {
		.fM(@mt:0, @mb: 58%, 0, 40px, 840px, 1280px);
	}
	}
	
}

.foo__input.main-btn {
	padding-top: 16px;
	padding-bottom: 16px;
}