@media screen and (max-width: 640px) {
	.center {
		padding: 0 15px;
	}

	.recomended__description {
		padding-top: 30px;
	}

	.recomended .center:last-child {
		margin-top: 0;
		padding-top: 45px;
		position: relative;
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(255,255,255,0.6);
		}
	}

	.recomended__card-link_second {
    box-shadow: 0 2px 10px -2px rgba(0,0,0,.45); 
}

	.recomended .center:last-child .f-container .real-flex-wrapper {
		width: 100%;
		justify-content: center;
		align-items: center;
		display: flex;
		flex-direction: column;
	}
	.real__btn {
		margin-right:auto ;margin-left:auto ;
	}
	.real__text {
		font-size: 16px;
		text-align: center;
		// br {
		// 	display: none;

		// }
	}

	.news__card-description {
		font-size: 13px;
	}

	.news__card-description {
		padding: 60px 25px;
	}

	.recomended__wrapper .slick-slide {
		margin: 0 15px;
	}

	.recomended__item {
		font-size: 14px;
		margin-right: 20px;
	}

	.header-controls__link-wrapper_usable {
		font-size: 12px;
	}

	.header-controls__search-wrapper, .header-controls__control {
		margin-right: 10px;
	}

	// .header-controls__search-wrapper {
	// 	min-width: 105px;
	// }

	.offer-form__select {
		font-size: 12px;
		padding-left: 24px;
	}

	.recomended_card-text {
		font-size: 13px;	
	}

	.offer-form__select-layer:first-child {
		margin-right: 	0;
		margin-bottom: 	25px;
	}

	.offer-form__select-wrapper {
		flex-direction: column;
	}

	.offer__h1 {
		font-size: 34px;
	}

	.offer {
		background-position:  58% center;
	}

	.h2-title {
		font-size: 41px;
		text-align: center;
		&:before {
			content: none;
		}
	}

	.offer-form__title {
		font-size: 23px;
	}

	.main-btn {
		font-size: 14px;
	}

	.knowledge__card-description {
		font-size: 13px;
	}

	.recomended .f-container:last-child {
		flex-direction: column;
		margin-top: 0;
	}

	.recomenden__content-left {
		margin-right: 0;
		margin-top: 30px;
	}

	.recomended__card-link_second:not(:last-child) {
		margin-bottom: 50px;
	}

	.footer__text {
		font-size: 14px;
	}

	.recomended__wrapper {
		.slick-track {
			padding: 25px 0;
		}
	}

	.recomended__wrapper {

		margin-top: 25px;
	}
	
	.recomended__wrapper .slick-slide {
		margin: 0 auto;
		max-width: 390px;
		width: 100% !important; 
	}

	.recomended__description-mobile {
		display: block;
	}

	.recomended__description-desctop {
		display: none;
	}

	.recomended__content-right {
		margin-top: 50px;
	}

	.menu__table-link_social {
		width: 50px;
		height: 50px;
		max-width: 50px;
		min-height: 50px;
	}
}


@media screen and (max-width: 460px) {
	.offer {
		& > * {
			position: relative;
			z-index: 3;	
		}
		position: relative;	
		&:before {
			content: "";
			height: 100%;
			top: 0;
			left: 0;
			position: absolute;
			background: rgba(255,255,255,0.75);	
			width: 	100%;
			z-index: 1;
		}
	}

	.header-controls__link-wrapper_usable, .header-controls__control {
		margin: 	0 auto;
	}

