.recomended {

	position: relative;	
	z-index: 5;
	padding-top: (156%/1920*50);
	.f-container:nth-child(2) {
		justify-content: center;
		align-items: center;

	}

	.f-container:last-child {
		// margin-top: -120px;
		flex-wrap: nowrap;
		flex-direction: row;
		margin-top: 30px;
		align-items: stretch;
	}

	.center:last-child {
		max-width: 1474px;
			background: url(../img/bride.png) no-repeat left;
	background-position: left bottom;
	margin-top: ((-282%)/1920*100);
	background-size:contain;
		padding-top: ((165% + 282%)/1920*100);
		padding-bottom: (125px);
		&:before {
		display: block;
		content: "";
	}

	.f-container {
		max-width: 1280px;
		margin: 	0 auto;
		// padding-top: 75px;
		flex-direction: column;
		margin-top: 0;
		// align-items: flex-start;
		// max-width: 800px;
		// margin-left: auto;
		// // min-height: 650px;
		justify-content: center;
		align-items: center;
		position: relative;
		& > * {
			position: relative;
			z-index: 3;
		}
		&:before {
			letter-spacing: 10px;
		    content: 'Реальні весілля';
		    position: absolute;
		    // left: -10px;
		    color: #ececec;
		    width: 100%;
		    font-size: (140vw/1280*100);
		    z-index: 0;
		    // width: 100vw;
		    left: 0;
		    font-family: Cormorant Infant,serif;
		    font-weight: 300;
		    top: -22%;
		    display: inline-block;
	}
		.real-flex-wrapper {
			margin-left: auto;
			width: 63%;
			max-width: 800px;
		}
	}
	}
}
.recomended__title {
	margin-bottom: 40px;
	position: relative;
	span {
		color: #e00058;
	}

	&:before {
	content: 'Рекомендовані';
    position: absolute;
    left: -10px;
    color: #ececec;
    font-size: (140vw/1280*100);
    z-index: -1;
    font-family: 'Cormorant Infant' , serif;
    font-weight: 400;
    top: -74%;
	}
}


.recomended__controls {
	.flex();
	margin: 0 auto;
}

.recomended__wrapper {
	width: 100%;
	.slick-slide {
		margin: 0 30px;
		width: 100%;
	}

	.slick-list {
		margin: 0 -30px;
	}

	.slick-track {
		.flex();
	

	}
	


}

.recomended__card-title {
	text-align: center;
	font-size: (30vw/1280*100);
	margin-top: 25px;
}

.recomended__card-outer {
	-webkit-box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    -moz-box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    margin: 0;
   
    background: #fff;
    max-width: 390px;
    width: 100%;
}

.recomended__description {
	max-width: 625px;
	// margin-left: auto;
	position: relative;	
	// top: 55px;
}

.recomended__card {
	margin: 0;
	 padding: 12px 12px 12px;
}

.recomended__img {
	margin: 0 auto;
}

.recomended__item {
	cursor: pointer;
	color: #a59e9e;
	font-weight: 600;
	margin-right: 45px;
	&:last-child {
		margin-right: 0;
	}
}

.recomended_card-text {
	font-family:  'MuseoSansRegular';
	font-weight: 300;
	text-align: center;
	margin-top: 10px;
	font-size: 14px;
	max-width: 325px;
	margin: 0 auto;
	margin-top: 10px;
	margin-bottom: 25px;
}

.recomended__wrapper {
	margin-top: 45px;
	.slick-list {
		padding: 25px 0;
	}
}


.active-item {
	font-weight: 600;
	color: #292828; 
	position: relative;
	&:before {
		position: absolute;
		content: "";
		width: 61px;
		height: 1px;
		background: #e00058;
		bottom: -7px;
		left: 50%;
		transform: translateX(-50%);
	}
}

.recomended__card_second {
	margin: 0;
}

.recomended__card-link_second {
	box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
	margin: 0;
	padding: 52px 52px 50px 52px;
	background: #fff;
	max-width: 614px;

	&:not(:last-child) {
		margin-bottom: 	70px;
	}

	

	// &:nth-child(even) {
