}

.recomended__outer-layer {
	max-width: 614px;
}

.recomended__card-title_second {
	font-size: (48vw/1280*100);
	position: relative;
	text-align: center;
	margin-top: 50px;
}

.recomended__card-title-wrapper_second {
	position: relative;
	text-align: center;
	// .flex(@justify-content:center);
}

.recomended__card-title_second_big {
	color: #e3e3e3;
	position: absolute;
	top: -52%;
	margin: auto;
	width: 	100%;
	left: 0;
	// transform: translateX(-50%);
	font-size: (80vw/1280*100);
}


.recomended__title_s {
	font-size: (36vw/1280*100);
	max-width: 75%;
