	margin: 0 auto;
	margin-bottom: 27px;
	// padding: 	70px 0;
}

.recomended__btn {
	    max-width: 338px;
    padding-top: 11px;
    padding-bottom: 11px;
    margin: 0 auto;
    text-align: center;
}

.recomenden__content-left {
	margin-right: 25px;
	height: auto;
}

.recomended__content-right {
	position: relative;	
	height: auto;
	// .recomended__card-link_second {
	// 	position: relative;	
	// 	top: 185px;
	// }
}

.recomended__description {
	padding: 70px 0;
}

.recomended__card-like {
	font-size: 14px;
	font-family: 'MuseoSansMedium', sans-serif;
	width: 50px;
	.flex(@justify-content:center);

	svg {
		width: 13px;
		height: 16px;
		fill: #292828;
		margin-right: 10px;
		transition: all 0.2s linear;
		&:hover {
		fill: #e00058;
		}
	}
}


.recomended__description-mobile {
	display: none;
}

