.about-more__h2 {
	font-family: 'Cormorant Unicase';
	font-weight: 400;
	.fSize(36vw, 24px, 36px, 640px, 1280px);
	text-align: center;
	position: relative;
	&:before {
		.ba();
		height: auto;
		content:"Крім того";
		color: #ececec;
		font-family: 'Cormorant Infant',serif;
    font-weight: 400;
    .fSize(140vw, 110px, 140px, 640px, 1280px);
    top:-100%;
    z-index: -1;
    display: block;
    top: -190%;
	 }
}

.about-more__top, .about-more__bottom {
	width: 	100%;
}

.about-more__card {

	.flex(@justify-content:center, @align-items: flex-start);
	.fM(@mt:0, @mb: 110%, 0, 40px, 480px, 1280px);

	&:first-child{
		margin-bottom: 25px;
			.about-more__descr{
				margin-right: 24%;
			}
	}

	&:nth-child(2) {
			.about-more__descr{
				margin-left: 60px;
			}

			.about-more__img {
				margin-left: 25.7%;
			}
	}

	&:nth-child(3){
			.about-more__descr{
				margin-left: 60px;
			}
	}

	&:nth-child(4){
		.fM(@mt:0, @mb: 70%, 0, 40px, 480px, 1280px);
			.about-more__descr{
				margin-left: 5.7%;
			}

			.about-more__img {
				margin-left: 5.7%;
			}
	}

	&:nth-child(5) {
			.about-more__descr{
				margin-left: 40px;
			}
	}

}

.about-more__img {
	// height: 15vh;
}

.about-more__descr {
	font-family:MuseoSansRegular, sans-serif;
	font-size: 16px;
	max-width: 318px;
	letter-spacing: 1px;
	.fP(@pt:55%, @pb: 0, 20px, 0, 480px, 1280px);
	line-height: 1.55;
	min-width: 318px;

}

.about-more__text {
	span {
		color: #e81d6f;
	}
}