.news {
	padding-bottom: 33px;
	padding-top: (140%/1920*100);
	.f-container {
		margin: 0 -38.5px;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
	}
}
.news__card {
	-webkit-box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    -moz-box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
    margin: 28px 32.5px 0;
    padding: 12px 12px 0;
    background: #fff;
    max-width: 387px;
}

.news__card-description {
	padding: 60px 55px;
	text-align: center;
	font-family: MuseoSansMedium,sans-serif;
    font-weight: 500;
    font-size: 16px;
}

.news__btn {
	margin-top: 72px;
}

.news__title {
	position: relative;	
	&:before {
		content: 'Новини та статті';
	    position: absolute;
	    left: 0px;
	    color: #e3e3e3;
	    letter-spacing: 12px;
	    font-size: 14.0625vw;
	    z-index: -1;
	    font-family: 'Cormorant',serif;
	    font-weight: 300;
	    top: -75%;
	    max-width: 115%;
	    width: 	115%;
	}
}